








































































import { Component, Vue } from 'vue-property-decorator';
import { mapGetters } from 'vuex';

import { ADD_TO_BASKET, REMOVE_FROM_BASKET } from '../store/types/mutations.type';

interface ICatalog {
  category?: ICategory;
}

@Component({
  props: ['category'],
  computed: mapGetters([
    'isDisabledAddButton',
    'isShowDeleteButton',
    'getFormattedPrice',
    'getQuantityFromBasketById',
  ]),
})
export default class ProductCard extends Vue<ICatalog> {
  isCollapsed: boolean = false;
  columns = [
    {
      title: 'Название',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'Доступно',
      dataIndex: 'available',
      key: 'available',
    },
    {
      title: 'Цена',
      dataIndex: 'price',
      key: 'price',
    },
    {
      title: 'Действия',
      dataIndex: 'actions',
      key: 'actions',
    },
  ];

  get products () {
    if (!this.category) {
      return [];
    }

    return this.$store.getters.getProductsByCategoryId(this.category.id);
  }

  toggleCollapsibleContent () {
    this.isCollapsed = !this.isCollapsed;
  }

  addToBasket (product: IProduct) {
    this.$store.commit(ADD_TO_BASKET, product);
  }

  removeFromBasket (id: number) {
    this.$store.commit(REMOVE_FROM_BASKET, id);
  }
}
